import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLoading: false,
    results: [],
  },
  getters: {
    results: (state) => state.results,
  },
  mutations: {
    ADD_RESULT(state, word) {
      state.results.push(word);
    },
  },
  actions: {
    addResult({
      commit,
    }, word) {
      commit('ADD_RESULT', word);
    },
  },
  modules: {},
});
