<template lang="pug">
  div
    .guessed-word
      span.guessed-letter(:class="{ break: letter == ' ' }" v-for="(letter, i) in word" :key="i")
        | {{ letter }}
</template>

<script>
export default {
  props: { currentWord: String, guessedWord: String },
  computed: {
    word() {
      const newWord = Array(this.currentWord.length);
      this.currentWord.split('').forEach((letter, index) => {
        if (this.currentWord[index].toUpperCase() === this.guessedWord[index]) {
          newWord[index] = letter.toUpperCase();
        } else if (this.currentWord[index] === ' ') {
          newWord[index] = ' ';
        } else {
          newWord[index] = '';
        }
      });
      return newWord;
    },
  },
};
</script>

<style lang="scss" scoped>
.guessed-word {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.guessed-letter {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-bottom: 2px solid #008cff;
  font-size: 28px;
  text-align: center;
  margin: 10px 5px 0px 5px;
}
.break {
  flex-basis: 100%;
  height: 0 !important;
  border: none !important;
  margin: 0 !important;
  font-size: 0 !important;
}

@media screen and (max-width: 720px) {
  .guessed-letter {
    width: 20px;
    height: 20px;
    border-bottom: 2px solid #008cff;
    font-size: 24px;
    text-align: center;
    margin: 30px 2px 40px 2px;
    padding-bottom: 5px;
  }
}
</style>
